@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

a:visited {
  color: grey;
}

a:hover {
  -webkit-text-decoration: orange underline;
          text-decoration: orange underline;
  text-decoration-thickness: 2px;
}

main {
  font-family: 'Poppins';
  text-align: center;
  background: #141414;
  color: #cfcfcf;
}

.principal {
  width: 60%;
  margin: auto;
  background: #1b1b1b;
  /* GLOBAL SETTINGS OF ALL SECTIONS */
  /* PARTICULAR SETTINGS OF EACH SECTION */
}

.principal header {
  padding: 1em;
  display: flex;
  align-items: center;
  place-content: space-between;
  /* 
    background: lightgreen; */
}

.principal header .logo-container {
  /* 
      background: rgb(163, 103, 103); */
  flex: 1 1;
}

.principal header .left {
  flex: 4 1;
}

.principal header .left .left-nav {
  display: flex;
  list-style-type: none;
}

.principal header .left .left-nav .nav-link {
  padding-right: 1em;
}

.principal header .right {
  flex: 4 1;
}

.principal header .right .right-nav {
  display: flex;
  padding-top: 0.4em;
  list-style-type: none;
  justify-content: end;
}

.principal header .right .right-nav .nav-link {
  padding-right: 0.5em;
}

.principal .hero, .principal .section2, .principal .roadmap, .principal .team, .principal .section5 {
  width: 100%;
  padding: 1em;
}

.principal .hero .sectionName, .principal .section2 .sectionName, .principal .roadmap .sectionName, .principal .team .sectionName, .principal .section5 .sectionName {
  font-size: 3em;
  margin-bottom: 1em;
  text-align: center;
}

.principal .hero {
  display: flex;
  align-items: center;
  place-content: space-between;
}

.principal .hero .left-col img {
  width: 20em;
  height: auto;
  border-radius: 1em;
}

.principal .hero .right-col {
  text-align: left;
  display: block;
  padding-left: 3em;
}

.principal .hero .right-col .mintButton {
  margin: 1em;
  text-align: center;
}

.principal .roadmap {
  width: 100%;
  display: block;
  text-align: left;
}

.principal .roadmap .roadItem {
  display: flex;
  padding-top: 2em;
  padding-bottom: 3em;
  padding-right: 1em;
  margin-bottom: 1em;
  background: #141414;
  border-radius: 1em;
}

.principal .roadmap .roadItem .roadLeftCol {
  text-align: right;
}

.principal .roadmap .roadItem .roadLeftCol .roadDate {
  font-size: 1em;
}

.principal .roadmap .roadItem .roadLeftCol .roadDesc {
  font-size: 1.3em;
}

.principal .roadmap .roadItem .roadRightCol {
  width: 100%;
  padding-left: 4em;
  text-align: left;
}

.principal .team .sectionContent {
  margin-top: 7em;
  display: flex;
}

.principal .team .sectionContent .teamMember {
  background: #141414;
  border-radius: 1em;
  padding: 1em;
  margin: 1em;
}

.principal .team .sectionContent .teamMember .memberPic img {
  width: 50%;
  height: auto;
  border: 5px solid #141414;
  border-radius: 50%;
  margin-top: -10em;
}

.principal .team .sectionContent .teamMember .memberName {
  align-items: center;
  display: block;
  font-weight: bold;
}

.principal .team .sectionContent .teamMember .memberName img {
  width: 2em;
  height: auto;
}

.principal .team .sectionContent .teamMember .memberTW {
  text-align: right;
}

.principal .team .sectionContent .teamMember .memberTW img {
  width: 2em;
  height: auto;
}

.principal .team .sectionContent .teamMember .memberDesc {
  font-style: italic;
  padding-top: 2em;
}

/* @media screen and (max-width:1120px){
  
  .principal{
    width: 60%;
  }
  .left{
    display: none;
  }
  
} */
@media screen and (max-width: 550px) {
  .principal {
    width: 90%;
  }
  .principal .logo-container {
    display: none;
  }
  .principal .left {
    display: none;
  }
  .principal .hero .sectionName, .principal .section2 .sectionName, .principal .roadmap .sectionName, .principal .team .sectionName, .principal .section5 .sectionName {
    font-size: 2.5em;
    margin-bottom: 0.5em;
    text-align: left;
  }
  .principal .hero {
    width: 100%;
    display: block;
    align-items: center;
    padding: 0;
  }
  .principal .hero .left-col img {
    width: 100%;
    height: auto;
    padding: 1em;
  }
  .principal .hero .right-col {
    padding-left: 0em;
    padding: 1em;
  }
  .principal .roadmap .roadItem {
    padding-left: 1em;
    display: block;
  }
  .principal .roadmap .roadItem .roadLeftCol {
    text-align: left;
  }
  .principal .roadmap .roadItem .roadLeftCol .roadDate {
    font-size: 1em;
  }
  .principal .roadmap .roadItem .roadLeftCol .roadDesc {
    font-size: 1.3em;
    margin-bottom: 1em;
  }
  .principal .roadmap .roadItem .roadRightCol {
    width: 100%;
    padding-left: 0;
    text-align: left;
  }
  .principal .team .sectionContent {
    margin-top: 7em;
    display: block;
  }
  .principal .team .sectionContent .teamMember {
    margin-bottom: 5em;
  }
}
